import { SubTitleBlue } from "../OrderCommonStyled";
import {
  BlockWrapper,
  Data,
  OrderDataWrapper,
  SmallSubTitle,
  Text,
} from "./OrderData.styled";

interface OrderDataProps {
  customerName: string;
  customerLastName: string;
  customerEmail: string;
  customerPhone: string;
  deliveryMethod: string;
  deliveryCity: string;
  deliveryAddress: string;
  paymentMethod: string;
  orderComment: string;
}

const OrderData: React.FC<OrderDataProps> = ({
  customerName,
  customerLastName,
  customerEmail,
  customerPhone,
  deliveryMethod,
  deliveryCity,
  deliveryAddress,
  paymentMethod,
  orderComment,
}) => {
  // console.log(deliveryMethod);
  return (
    <OrderDataWrapper>
      <SubTitleBlue>  </SubTitleBlue>

      <BlockWrapper>
        <SmallSubTitle></SmallSubTitle>
        <Data>
          <span>{customerName ? customerName : " "}</span>{" "}
          <span>{customerLastName ? customerLastName : " "}</span>
        </Data>
        <Data>{customerEmail ? customerEmail : " email"}</Data>
        <Data>{customerPhone ? customerPhone : "  "}</Data>
      </BlockWrapper>

      <BlockWrapper>
        <SmallSubTitle></SmallSubTitle>
        <Text>{deliveryMethod && deliveryMethod}</Text>
        <Data>
          {deliveryCity && deliveryAddress
            ? `${deliveryCity} ${deliveryAddress}`
            : "  "}
        </Data>
      </BlockWrapper>

      <BlockWrapper>
        <SmallSubTitle></SmallSubTitle>
        <Data>{paymentMethod ? paymentMethod : "  "}</Data>
      </BlockWrapper>

      <BlockWrapper>
        <SmallSubTitle></SmallSubTitle>
        <Data>
          {orderComment ? orderComment : "  ()"}
        </Data>
      </BlockWrapper>
    </OrderDataWrapper>
  );
};

export default OrderData;
